@import "Style/constants";

.wrapper-select {
  &_default {
    height: 3.14rem;
    width: 22.86rem;
    box-sizing: border-box;
  }
}
// .dropdown__main:after {
//   content: "\e5cf";
//   color: $color-shade-third;
//   font-size: 24px;
//   font-family: "Material Icons";
//   pointer-events: none;
// }
select:-ms-expand {
  display: none;
}

.dropdown {
  color: black;
  display: inline-block;
  color: $color-shade-second;
  transition: border-color 0.4s;
  position: relative;
  &__main {
    display: flex;
    align-items: center;
    border-radius: 0.29rem;
    border: 0.07rem solid $color-shade-forth;
    position: relative;
    &::after {
      position: absolute;
      font-family: "Material Icons";
      font-size: 1.71rem;
      padding: 0 0.71rem;
      color: $color-shade-third;
      content: "\e5cf";
      top: 50%;
      transform: translate(0, -50%);
      right: 0;
    }
    .input {
      border: none;
    }
    &:hover {
      border-color: $color-shade-second;
    }
  }
  &__title {
    flex: 1;
  }
  &__content {
    background-color: white;
    visibility: hidden;
    position: absolute;
    z-index: 2;
    width: 100%;
    color: $color-shade-second;
    padding: 0.5rem;
    transition: border-color 0.4s;
    box-sizing: border-box;
  }
}
.show {
  .dropdown__main {
    border-radius: 0.29rem 0.29rem 0 0;
    border-color: $color-shade-second;
  }
  border-radius: 0.29rem 0.29rem 0 0;
  border-color: $color-shade-second;
  .dropdown__content {
    visibility: visible;
    border-radius: 0 0 0.29rem 0.29rem;
    border: 0.07rem solid $color-shade-second;
    border-top: none;
  }
}
